.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 250px;
  padding: 20px;
  border: 5px solid #558cdd;
  border-radius: 20px;
  margin: 40px auto;
  background-color: yellowgreen;
}

.label {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  font-weight: bold;
  font-style: italic;
  margin-bottom: 10px;
  color: aliceblue;
}

.input {
  width: 250px;
  height: 26px;
  padding: 6px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  outline: none;
  font-size: 16px;
}

.button {
  border-radius: 20px;
  display: inline-block;
  border: 2px solid #104ba3;
  cursor: pointer;
}
